<template>
  <div id="message-card" :class="{ inactive: !active, active: active }">
    <p>{{ msg }}</p>
  </div>
</template>

<script>
export default {
  props: ["msg", "active"],
  data: function () {
    return {};
  },
};
</script>

<style>
#message-card {
  position: fixed;
  z-index: 997;
  bottom: 32px;
  left: 50%;
  max-width: 95vw;
  white-space: nowrap;
  transform: translateX(-50%);
  padding: 12px;
  background-color: white;
  border-radius: 12px;
  font-size: 16px;
}

#message-card.inactive {
  filter: opacity(0);
  transition: all 0.5s;
  pointer-events: none;
}
#message-card.active {
  filter: opacity(1);
  transition: all 0.5s;
}
</style>
