<template>
  <div id="masil-page">
    <div id="map"></div>
    <div id="floatmenu-container">
      <div>
        <div class="select-boxes">
          <v-select
            :placeholder="`큰 분류`"
            :options="bigFilterOpts"
            label="name"
            v-model="selectedBigFilter"
            :clearable="false"
            v-on:input="clearSmallFilter"
          ></v-select>
          <v-select
            :placeholder="`작은 분류`"
            :options="smallFilterOptKeys"
            v-model="selectedSmallFilter"
            :clearable="false"
            :disabled="selectedBigFilter ? false : true"
          ></v-select>
        </div>
        <input
          class="free-response"
          type="text"
          placeholder="어디로 갈까요?"
          v-model="freeResponse"
          v-show="selectedSmallFilter === `직접입력`"
        />
      </div>

      <div class="btns" style="display: flex">
        <button class="circle-btn" @click="selectPoint" type="button">
          <img :src="img.marker" alt="select departure point" />
        </button>
        <button class="circle-btn" @click="startSelectingDistance" :disabled="!selectedPointCoord">
          <img :src="img.area" alt="set radius" style="width: 28px; height: 28px" />
        </button>
        <button
          id="submit-plan-btn"
          class="circle-btn"
          :disabled="!selectedDistance || !selectedSmallFilter"
          @click="buildPlaceRequest"
        >
          <img :src="img.airplane" alt="submit user plan, request place to server" />
        </button>
      </div>
    </div>
    <message-card :msg="msgCard.msg" :active="msgCard.isActive"></message-card>
  </div>
</template>

<script>
import { bigFilterOpts, smallFilterOpts, drawing, apiRequestURL } from "@/assets/variables.js";
import markerImg from "@/assets/marker.png";
import airplaneImg from "@/assets/airplane_white.png";
import areaImg from "@/assets/area.png";

import MessageCard from "@/components/MessageCard.vue";

export default {
  components: {
    "message-card": MessageCard,
  },
  data: function () {
    return {
      map: null,
      selectedPointCoord: null,
      selectedPointMarker: null,
      selectedDistance: null,
      drawing: null,

      bigFilterOpts: bigFilterOpts,

      selectedBigFilter: null,
      selectedSmallFilter: null,
      freeResponse: null,

      img: {
        marker: markerImg,
        airplane: airplaneImg,
        area: areaImg,
      },

      msgCard: {
        msg: "",
        isActive: false,
      },
    };
  },
  computed: {
    smallFilterOptKeys: function () {
      if (this.selectedBigFilter) {
        const code = this.selectedBigFilter.code;
        return smallFilterOpts[code];
      } else {
        return [];
      }
    },
    isDrawing: function () {
      return this.drawing !== null;
    },
  },
  methods: {
    clearSmallFilter: function () {
      this.selectedSmallFilter = null;
    },
    showMessageCard: function (_msg) {
      this.msgCard = {
        msg: _msg,
        isActive: true,
      };
    },
    hideMessageCard: function () {
      this.msgCard = {
        ...this.msgCard,
        isActive: false,
      };
    },
    selectPoint: function () {
      const vm = this;
      if (vm.isDrawing) vm.resetSelectedDistance();

      // 마우스커서 + 십자모양으로.
      vm.map.setCursor("crosshair");

      // 기존 기준점 마커가 있다면 삭제
      if (vm.selectedPointMarker) {
        vm.selectedPointMarker.setMap(null);
        vm.selectedPointMarker = null;
        vm.selectedPointCoord = null;
      }

      function handleClick(mouseEvent) {
        window.kakao.maps.event.removeListener(vm.map, "click", handleClick);

        // 마커 그리기
        if (!mouseEvent.latLng) return;
        /* else */ const latlng = mouseEvent.latLng;

        vm.selectedPointCoord = latlng;
        vm.selectedPointMarker = new window.kakao.maps.Marker({
          position: vm.map.getCenter(),
        });
        vm.selectedPointMarker.setMap(vm.map);
        vm.selectedPointMarker.setPosition(latlng);

        // 지도 중심을 마커 좌표로 옮기기
        var moveLatLon = new window.kakao.maps.LatLng(latlng.Ma, latlng.La);
        vm.map.panTo(moveLatLon);

        // 마우스커서 원래대로
        vm.map.setCursor("grab");

        vm.hideMessageCard();
      }
      vm.showMessageCard("출발할 곳을 클릭해 골라주세요");
      window.kakao.maps.event.addListener(vm.map, "click", handleClick);
    },
    startSelectingDistance: function () {
      const vm = this;
      vm.resetSelectedDistance();

      // 새로운 line, pickingCircle, pickingOverlay 생성
      vm.drawing = {
        // line, picking Circle/Overlay : 마우스 움직임으로 측정된 거리를 표현하는 선, 원, 거리값 숫자.
        line: new window.kakao.maps.Polyline(drawing.line),
        pickingCircle: new window.kakao.maps.Circle(drawing.pickingCircle),
        pickingOverlay: new window.kakao.maps.CustomOverlay(drawing.overlay),

        // saved Circle/Overlay : 마우스 클릭으로 저장된 거리를 표현하는 원 + 거리값 숫자.
        savedCircle: new window.kakao.maps.Circle(drawing.savedCircle),
        savedOverlay: new window.kakao.maps.CustomOverlay(drawing.overlay),
      };

      vm.showMessageCard("어디까지 갈 수 있는지 클릭해 알려주세요");
      window.kakao.maps.event.addListener(vm.map, "mousemove", vm.drawPickingCircle); // 거리 재기
      window.kakao.maps.event.addListener(vm.map, "click", vm.drawSavedCircle); // 거리 저장
    },
    drawPickingCircle: function (mouseEvent) {
      const vm = this;

      const line = vm.drawing.line;
      const pickingCircle = vm.drawing.pickingCircle;
      const pickingOverlay = vm.drawing.pickingOverlay;
      const centerPosition = vm.selectedPointCoord;
      var mousePosition = mouseEvent.latLng;
      var linePath = [centerPosition, mousePosition];

      line.setPath(linePath);
      var length = line.getLength();

      var circleOptions = {
        center: centerPosition,
        radius: length,
      };
      pickingCircle.setOptions(circleOptions);
      var radius = Math.round(pickingCircle.getRadius());

      var content =
        '<div style="padding : 2px; background-color : white; border-radius : 4px; transform:translateX(10px);">거리 <span style="color : #845ec2; font-weight : bold;">' +
        radius +
        "</span>m</div>";

      pickingOverlay.setPosition(mousePosition);
      pickingOverlay.setContent(content);

      line.setMap(vm.map);
      pickingCircle.setMap(vm.map);
      pickingOverlay.setMap(vm.map);
    },
    drawSavedCircle: function (event) {
      const vm = this;

      const centerPosition = vm.selectedPointCoord;
      const savedCircle = vm.drawing.savedCircle;
      const savedOverlay = vm.drawing.savedOverlay;

      var mousePosition = event.latLng;
      var linePath = [centerPosition, mousePosition];
      var line = new window.kakao.maps.Polyline(drawing.line);
      line.setPath(linePath);
      var length = line.getLength();

      var circleOptions = {
        center: centerPosition,
        radius: length,
      };
      vm.selectedDistance = Math.round(length);

      savedCircle.setOptions(circleOptions);

      var content =
        '<div style="padding : 2px; background-color : white; border-radius : 4px; transform:translate(-50%, 10px);">선택된 거리 <span style="color : #845ec2; font-weight : bold;">' +
        vm.selectedDistance +
        "</span>m</div>";

      savedOverlay.setPosition(centerPosition);
      savedOverlay.setContent(content);
      savedCircle.setMap(vm.map);
      savedOverlay.setMap(vm.map);

      this.stopSelectingDistance();
    },
    stopSelectingDistance: function () {
      const vm = this;

      if (vm.drawing) {
        vm.drawing.line && vm.drawing.line.setMap(null);
        vm.drawing.pickingCircle && vm.drawing.pickingCircle.setMap(null);
        vm.drawing.pickingOverlay && vm.drawing.pickingOverlay.setMap(null);
      }
      // 이벤트리스너 모두 제거
      window.kakao.maps.event.removeListener(vm.map, "mousemove", vm.drawPickingCircle); // 거리 재기
      window.kakao.maps.event.removeListener(vm.map, "click", vm.drawSavedCircle); // 거리 저장

      vm.hideMessageCard();
    },
    resetSelectedDistance: function () {
      const vm = this;
      vm.selectedDistance = null;

      if (vm.drawing) {
        vm.drawing.line && vm.drawing.line.setMap(null);
        vm.drawing.pickingCircle && vm.drawing.pickingCircle.setMap(null);
        vm.drawing.pickingOverlay && vm.drawing.pickingOverlay.setMap(null);
        vm.drawing.savedCircle && vm.drawing.savedCircle.setMap(null);
        vm.drawing.savedOverlay && vm.drawing.savedOverlay.setMap(null);
        vm.drawing = null;
      }
      // 이벤트리스너 모두 제거
      window.kakao.maps.event.removeListener(vm.map, "mousemove", vm.drawPickingCircle); // 거리 재기
      window.kakao.maps.event.removeListener(vm.map, "click", vm.drawSavedCircle); // 거리 저장

      vm.hideMessageCard();
    },
    buildPlaceRequest: function () {
      const url = apiRequestURL.placeByUserinput;
      const params = {
        x: this.selectedPointCoord.Ma,
        y: this.selectedPointCoord.La,
        dis: this.selectedDistance,
        bigfilter: this.selectedBigFilter.code,
        smallfilter: null,
      };

      // smallfilter값 결정
      const sf = this.selectedSmallFilter;
      if (sf !== "어디로든") {
        if (sf === "직접입력") params.smallfilter = this.freeResponse ? this.freeResponse : null;
        else params.smallfilter = this.selectedSmallFilter;
      }

      // 가상의 요청. (alert으로 보여주는 것으로 대신함.)
      // axios.get( <something> )
      const buildResult = `아래와 같이 요청데이터를 만들었습니다.
      url : ${url}\n
      params : 
      - x : ${params.x}
      - y : ${params.y}
      - dis : ${params.dis}
      - bigfilter : ${params.bigfilter}
      - smallfilter : ${params.smallfilter}
      `;
      alert(buildResult);
      console.log(buildResult);
    },
  },
  async mounted() {
    if (!this.map) {
      const SJU_COORD = { lat: 37.5507377, lng: 127.0742838 }; // 세종대학교 위경도좌표
      const center = await getMapCenter({ defaultLat: SJU_COORD.lat, defaultLng: SJU_COORD.lng });

      var container = document.getElementById("map");
      var options = {
        center: new window.kakao.maps.LatLng(center.lat, center.lng),
        level: 3,
      };
      this.map = new window.kakao.maps.Map(container, options);
    }
  },
};

function getUserPos() {
  return new Promise((resolve, reject) => {
    navigator.geolocation.getCurrentPosition(resolve, reject);
  });
}

async function getMapCenter({ defaultLat, defaultLng }) {
  const center = { lat: 0, lng: 0 };
  try {
    if ("geolocation" in navigator) {
      const pos = await getUserPos();
      center.lat = pos.coords.latitude;
      center.lng = pos.coords.longitude;
    } else {
      center.lat = defaultLat;
      center.lng = defaultLng;
    }
  } catch (err) {
    center.lat = defaultLat;
    center.lng = defaultLng;
  }

  return center;
}
</script>

<style lang="scss">
#masil-page {
  flex: 1;

  #map {
    height: 100%;
    width: 100%;
  }

  #floatmenu-container {
    position: fixed;
    top: $header-height;
    left: 0px;
    z-index: 998;

    display: flex;
    flex-direction: column;
    padding: 8px;
    margin: 16px;
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 16px;

    .select-boxes {
      display: flex;
    }
    .v-select {
      z-index: 998;
      margin-right: 8px;
      min-width: 140px;
      --vs-dropdown-min-width: 140px;
      // in @/assets/variables.scss
      // $c-highlight-bg: #845ec2;
      // $c-highlight-text: #fbeaff;
      --vs-dropdown-option--active-bg: #845ec2;
      --vs-dropdown-option--active-color: #fbeaff;
      --vs-search-input-placeholder-color: grey;
      --vs-border-radius: 16px;
      --vs-font-size: 14px;
      --vs-line-height: 24px;
      transition: all 0.2s;
    }
    .v-select:hover {
      transform: scale(1.05);
    }

    .free-response {
      width: calc(100% - 8px);
      height: 36px;

      padding: 8px;
      margin-top: 8px;

      border-radius: 16px;
      border: 1px solid rgba(60, 60, 60, 0.26);
      font-size: 14px;
      transition: all 0.2s;
    }
    .free-response:focus {
      outline: none;
      transform: scale(1.05);
    }
    .free-response:hover {
      transform: scale(1.05);
    }
  }

  .circle-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 36px;
    height: 36px;
    margin-right: 8px;
    margin-top: 8px;

    background-color: white;
    border-radius: 50%;
    border: 1px solid rgba(60, 60, 60, 0.26);
    cursor: pointer;
    transition: all 0.2s;
    img {
      width: 22px;
      height: 22px;
      filter: opacity(0.8);
    }
  }
  .circle-btn:hover {
    transform: scale(1.1);
  }
  .circle-btn:active {
    transform: scale(0.9);
  }
  .circle-btn:disabled {
    cursor: not-allowed;
    filter: brightness(70%);
  }

  #submit-plan-btn {
    background-color: $c-highlight-bg;
    color: $c-highlight-text;
    margin-left: auto;
    img {
      transform: translateX(-1px);
    }
  }

  @media (max-width: 768px) {
    #floatmenu-container {
      width: 100vw;
      margin: 0;
      align-items: center;
      font-size: 14px;
      background: none;
      border-radius: 0;

      .btns {
        width: 296px;
      }
    }
  }
}
</style>
